const { auto } = require("@popperjs/core");

const swiper = new Swiper('.swiper', {
  // Optional parameters
  direction: 'horizontal',
  loop: false,
  slidesPerView: 4,
  spaceBetween: 30,
  slidesPerGroup: 1,

  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },

  breakpoints: {
    // when window width is >= 320px
    320: {
      slidesPerView: 2,
      spaceBetween: 20
    },
    // when window width is >= 480px
    480: {
      slidesPerView: 2,
      spaceBetween: 30
    },
    // when window width is >= 640px
    1260: {
      slidesPerView: 4,
      spaceBetween: 30
    }
  }

});

const swiperMobile = new Swiper('.swiper-320', {

  direction: 'horizontal',
  loop: false,
  slidesPerView: 'auto',
  spaceBetween: 15,
})
