const tabs = document.querySelectorAll('.guests__accordion-item')
const guests = document.querySelectorAll('.guests__card')

tabs.forEach(element => {
  element.addEventListener('click', () => {
    let tabId = element.getAttribute('data-tab')
    let currentGuest = document.querySelector(tabId)

    tabs.forEach(tab => {
      tab.classList.remove('active-tab')
    })

    guests.forEach(guest => {
      guest.classList.remove('active-guest')
    })

    element.classList.add('active-tab')
    currentGuest.classList.add('active-guest')
  })
})
