const etherMusicBtn = document.querySelectorAll('.header__btn-ether'),
  podcastsMusicBtn = document.querySelectorAll('.podcasts__card-btn')

function play(button) {
  button.forEach(btn => {
    btn.addEventListener('click', (e) => {
      const playBtn = e.currentTarget.querySelector('.play-btn')
      const pauseBtn = e.currentTarget.querySelector('.pause-btn')
      if (playBtn.classList.contains('current-btn')) {
        playBtn.classList.remove('current-btn')
        pauseBtn.classList.add('current-btn')
      } else {
        pauseBtn.classList.remove('current-btn')
        playBtn.classList.add('current-btn')
      }
    })
  })
}

play(etherMusicBtn)
play(podcastsMusicBtn)
